var Vijs = Vijs || {};

Vijs.videoPlayer = Vijs.videoPlayer || {
	init:function (adUnit,adUnitData,callback) {
		var player = this;
		var ad = adUnitData['ads'][0];
		var video = {
			videoUrl:'',
			loadComplete:false,
			warpper:null,
			onload:null,
			hasBlock:false,
			rewardHost:ad['notice_url'].split('/')[2],
			setting:adUnitData['setting'],
			tracking:ad['ad_tracking']||{"play_percentage": [{"rate": 0,"url": ""}, {"rate": 25,"url": ""}, {"rate": 50,"url": ""}, {"rate": 75,"url": ""}, {"rate": 100,"url": ""}],"mute": [""],"unmute": [""],"endcard_show": [""],"close": [""],"pause": [""]},
			preload: function(offer){
				var me = this;
				var videoUrl = player.decodeUrl(offer['video_url']);
				me.videoUrl = videoUrl;
				var startLoadTime = (new Date()).getTime();
				Vijs.Util.preloadResource(videoUrl,function(){
					me.loadComplete = true;
					if(callback) callback();
					if(me.onload) me.onload();
					var endLoadTime = (new Date()).getTime();
					var loadDuration = endLoadTime - startLoadTime;

	         		Vijs.Logger.post({
						'eventType':'videoLoadSuccess',
						'offer_id':ad['id'],
						'duration':loadDuration,
						'video_size':ad['video_size'],
					});

				},function(reason){
					Vijs.Logger.post({
						'eventType':'videoLoadFail',
						'offer_id':ad['id'],
						'videoUrl':videoUrl,
						'reason':reason? reason.type.toString() :'load error',
						'notice_url':ad['notice_url'],
						'unit_id':ad['unitid']
					});
				});
			},
			render:function(videoUrl){
				var me = this;
				var videoUrl = videoUrl || me.videoUrl;
				var frameWarpper = Vijs.Util.domHelper('div');
		        var frame = Vijs.Util.domHelper('iframe');
		        me['warpper'] = frameWarpper;

		        frameWarpper.css({
		          'position': 'fixed',
		          'z-index': 2147483646,
		          'top': '0px',
		          'left': '0px',
		          'right': '0px',
		          'bottom': '0px',
		          'margin':0,
		          'padding':0,
		          'border':0
		        });
		        var frameWidth = Vijs.Configer['currentScreen']['width'];
		        var frameHeight = Vijs.Configer['currentScreen']['height'];
		        frame.css({
		          'width': '100%',
		          'height': '100%',
		          'margin': '0',
		          'padding': '0',
		          'border': 'none',
		          'margin':0,
		          'padding':0,
		          'border':0
		        }).insertTo(frameWarpper);
		        frameWarpper.insertTo(document.body);
		        var documentContent = 
		        '<!doctype html><html lang="en"><head><meta charset="UTF-8"><title>video player</title><meta name="viewport" content="width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=no"><style type="text/css">*{margin:0;border:0;padding: 0;}html,body,.bg,.warp{display: block;width: 100%;height: 100%;}.bg{background-image: url('+ad['image_url']+');position: absolute;z-index: 2;width: 100%;height: 100%;-moz-filter: blur(40px);-webkit-filter: blur(40px);-o-filter: blur(40px);-ms-filter: blur(40px);filter: blur(40px);background-repeat: no-repeat;background-size: cover;}.warp{position: absolute;z-index: 999;}.center{display: -webkit-box;-webkit-box-orient: horizontal;-webkit-box-pack: center;-webkit-box-align: center;display: -moz-box;-moz-box-orient: horizontal;-moz-box-pack: center;-moz-box-align: center;display: -o-box;-o-box-orient: horizontal;-o-box-pack: center;-o-box-align: center;display: -ms-box;-ms-box-orient: horizontal;-ms-box-pack: center;-ms-box-align: center;display: box;box-orient: horizontal;box-pack: center;box-align: center;}video{display: block;width: 100%;height: 100%;position:relative;}#videoContainer{position: relative;}#videocover{position:absolute;z-index:100;left:0;right:0;top:0;bottom:0;background-color:transparent;}.playContainer {position: relative;float: left;padding: 0px;}.playContainer li {position: relative;float: left;border: 60px solid rgba(0, 0, 0, 0.3);color: rgba(255, 0, 0, 0.3);height: 0;width: 0;-webkit-border-radius: 100%;-moz-border-radius: 100%;-o-border-radius: 100%;border-radius: 100%;margin: 0 20px;}.playContainer span {	border-style: solid;	text-indent: -9999px;	position: absolute;	top: -24px;	left: -9px;}.playBtn span {border-color: transparent transparent transparent #fff;border-width: 24px 0 24px 36px;width: 0;height: 0;}</style></head><body><div class="bg"> </div><div class="warp center"><div id="videoContainer"><video id="mvvideoplayer" webkit-playsinline="true" playsinline="true" preload="metadata" src="'+videoUrl+'" poster="'+ad['image_url']+'"></video><div id="videocover" class="center"><section class="playContainer"><li class="playBtn"><span href="#" title="start">Start</span></li></section></div></div></div></body></html>';

		        
		        // rewardvideo.onclick = function(){videocover.style.display = 'none';rewardvideo.play();};
		        var rewardvideo;
		        var videocover;
		        initPlayView();
		        function initPlayView(){
		        	var percentTracking = me['tracking']['play_percentage'];
		        	frame.el.contentWindow.document.documentElement.innerHTML = documentContent;
			        var rewardvideo = frame.el.contentWindow.document.getElementById('mvvideoplayer');
			        rewardvideo.autoplay = true;
			        var videocover = frame.el.contentWindow.document.getElementById('videocover');

			        function playRewardVideo(){
			        	if(rewardvideo.getAttribute('hasplay')!=1){
			        		Vijs.Logger.post({
	        					url:percentTracking['endcard_show']
	        				});
			        	};
        				videocover.style.display = 'none';
        				if(rewardvideo.paused) rewardvideo.play();
        				rewardvideo.setAttribute('hasplay','1');
        			}

	        		me.videoBlockChecker.check({
			        	'video':rewardvideo,
			        	'videocover':videocover,
			        	'onload':function(){
			        		
			        		setTimeout(function(){
			        			if(rewardvideo.getAttribute('hasplay')!=1){
			        				videocover.style.display = '-webkit-box';
			        			}
			        			videocover.onclick = playRewardVideo
			        			videocover.ontouchend = playRewardVideo;
			        		},300);
			        	},
			        	'onplay':function(){
			        		playRewardVideo();
			        	},
			        	'onprocessing':function(percentage){
			        		var precent = parseFloat(percentage)*100;
			        		for(var i=0,iMax=percentTracking.length;i<iMax;i++){
			        			if((i+1<iMax)&&precent>percentTracking[i]['rate']&&precent<percentTracking[i+1]['rate']&&percentTracking[i]['send']!=true){
			        				percentTracking[i]['send'] = true;
			        				Vijs.Logger.post({'url':percentTracking[i]['url']});
			        			}
			        		}
			        	},
			        	'onend':function(){
			        		adUnit.endcardPlayer.play();
			        		Vijs.Logger.post({
								'eventType':'videoEnd',
								'offer_id':ad['id'],
								'notice_url':ad['notice_url'],
								'unit_id':adUnit['unitid'],
								'video_length':ad['video_length']
							});
							Vijs.Logger.post({url:percentTracking[percentTracking.length-1]['url']});
							video.addReward(adUnit['rewardedCallback'],me);
			        		me.close();
			        	},
			        	'onblock':function(){
			        		if(video.hasBlock==false){
			        			Vijs.Logger.post({
									'eventType':'videoStuck',
									'offer_id':ad['id'],
									'view_length':video.currentTime,
									'video_length':ad['video_length'],
									'unit_id':ad['unitid'],
									'notice_url':ad['notice_url']
								});
								video.hasBlock = true;
								video.showClose();
			        		} 
							
			        	},
			        	'onpause':function(){

			        	}
			        });
		        }

		        function SetVideoSize(video_resolution){
		        	window.setTimeout(function(){
		        		if(frame.el.contentWindow.document.documentElement.innerHTML.length<100) initPlayView();

		        		var video_resolution = video_resolution || ad['video_resolution'] || '1280x720';
			    		var videoContainerEl = frame.el.contentWindow.document.getElementById('videoContainer');
			    		var videoWidth = parseInt(video_resolution.split('x')[0]);
			    		var videoHeight = parseInt(video_resolution.split('x')[1]);
			    		var videoScale = videoWidth/videoHeight;
			    		var windowWidth = frame.el.contentWindow.innerWidth;
			    		var windowHeight = frame.el.contentWindow.innerHeight;
			    		var windowScale = windowWidth/windowHeight;
			    		if(videoScale>windowScale){
			    			videoContainerEl.style.width = '100%';
			    			videoContainerEl.style.height = Math.floor(windowWidth/videoScale)+'px';
			    		}else{
			    			videoContainerEl.style.width = Math.floor(videoScale*windowHeight)+'px';
			    			videoContainerEl.style.height = '100%';
			    		}
		        	},100);
		    		
		    	}

		    	SetVideoSize(ad['video_resolution']);
		    	frame.el.contentWindow.addEventListener('resize',function(){
		    		SetVideoSize(ad['video_resolution']);
		    	},false);

		        Vijs.Logger.post({
					url:ad['impression_url']
				});
		        
			},
			play:function(videoUrl){
				this.render(videoUrl);
				this.showClose();
			},
			pause:function(){

			},
			showClose:function(){
				var me = this;
				var frameWarpper = this['warpper'];
				var frameWarpperSize = frameWarpper.size();
				var closeWidth = Math.floor(frameWarpperSize['width'] / 15);
				var closeShowTime = this['setting']['video_skip_time'];
				if(me.clearCounting){
					me.clearCounting();
					closeShowTime = -100;
				} 
				me.clearCounting = Vijs.Render.addCountingCloseButton(frameWarpper,closeShowTime, closeWidth,'righttop',function(){
					me.close();
					if(me['videoBlockChecker'].clearTimmer) me['videoBlockChecker'].clearTimmer();
					if(adUnit.reload && me['loadComplete'] == true){ me['loadComplete'] = false; adUnit.reload(); } 
				});
			},
			close:function(){
				var me = this;
				var reason = this.hasBlock? 2:1;
        		Vijs.Logger.post({
					'eventType':'videoClose',
					'offer_id':ad['id'],
					'notice_url':ad['notice_url'],
					'unit_id':adUnit['unitid'],
					'view_length':video.currentTime,
					'video_size':ad['video_size'],
					'reason':reason,//1.skip/2.break
				});

        		if(adUnit['event']&&adUnit['event']!='*'&&adUnit['event']!='**') Vijs.Util.domHelper(window).unbind(adUnit['event'],playVideo);
				if(this['warpper']&&this['warpper'].remove()) this['warpper'].remove();

        		if(me['videoBlockChecker'].timmer.length>0){
        			me['videoBlockChecker'].clearTimmer();
        		}

			},
			addReward:function(rewardedCallback,me){
				if(me['setting']['is_incent']!=1) return;//non-reward video

				Vijs.Logger.post({
					'eventType':'reward',
					'url':Vijs.Configer['protocol']+me['rewardHost']+Vijs.Configer.getUrl('addrewardurl'),
					'user_id':player.encodeUrl(Vijs.Configer['rvuserid']),
					'click_id':Vijs.Util.getQueryString(ad['notice_url'],'k'),
					'cb_type':me['setting']['is_server_call'],
					'unit_id':adUnit['unitid'],
					'reward_name':me['setting']['reward'][0]['name'],
					'reward_amount':me['setting']['reward'][0]['amount']
				});
				if(
					(me['setting']['is_server_call']==2||Vijs.Configer['publisher']['ignoreRewardCallLocalOrServer'])&&
					rewardedCallback
					) rewardedCallback(me['setting']['reward'][0]['name'],me['setting']['reward'][0]['amount'],adUnit['unitid']);
			},
			videoBlockChecker:{
				interval:1000,
				bufferTime:adUnitData['setting']['hang']*1000,
				starter:{},
				timmer:{},
				videoEle:{},
				currentTime:0,
				clearTimmer:null,
				videocover:null,
				check:function(param){
					var video = param['video'];
					var me = this;
					me.videoEle = param['video'];
					me.videocover = param['videocover'];
					var counter = 0;
					var interval = me.interval/1000;
					var bufferTime = me.bufferTime/1000;
					me.starter[video] = video.currentTime;
					me.timmer[video] = window.setInterval(function(){
						Vijs.Configer.isLogShow && console.log(video.currentTime>=video.duration);

						if(me.videoEle.getAttribute('hasplay')=='1'&& !video.paused) counter += interval;

						var videoProcess = video.currentTime/video.duration;
						if(param['onprocessing']) param['onprocessing'](videoProcess);
						console.log(video.currentTime);
						if(video.currentTime>=video.duration){
							onPlayEnded();
							window.clearInterval(me.timmer[video]);
							delete me.starter[video];
							delete me.timmer[video];
							return;
						}
						// console.log(video.currentTime)
						// console.log('--'+Math.abs(me.starter[video]+counter-video.currentTime))
						if( Math.abs(me.starter[video]+counter-video.currentTime)>bufferTime&&me.videoEle.getAttribute('hasplay')=='1'){
							
							counter = video.currentTime+bufferTime - me.starter[video];
							if(param['onblock']) param['onblock']();
							Vijs.Configer.isLogShow && console.warn('video block!');
							// window.clearInterval(me.timmer[video]);
							// delete me.starter[video];
							// delete me.timmer[video];
						}
					},me.interval);

					video.addEventListener('pause', videoPause, false);
					video.addEventListener('ended', clearTimmer, false);

					function videoPause(){
						if(param['onpause']) param['onpause']();
						Vijs.Logger.post({
							url:ad['ad_tracking']['pause']
						});
					}

					function clearTimmer(){
						if(video.currentTime>=video.duration) onPlayEnded();
						window.clearInterval(me.timmer[video]);
						delete me.starter[video];
						delete me.timmer[video];
						video.removeEventListener('pause', clearTimmer, false);
						video.removeEventListener('ended', clearTimmer, false);
					}

					function onPlayEnded(){
						window.clearInterval(me.timmer[video]);
						delete me.starter[video];
						delete me.timmer[video];
						if(param['onend']) param['onend']();

						Vijs.Configer.isLogShow && console.log('video end!')
					}

					video.onloadedmetadata = function(){
						if(param['onload']) param['onload']();
					}

					video.onplay = function(){
						if(param['onplay']) param['onplay']();
					}

					var hiddenProperty = 'hidden' in document ? 'hidden' :    
					    'webkitHidden' in document ? 'webkitHidden' :    
					    'mozHidden' in document ? 'mozHidden' :    
					    null;
					var visibilityChangeEvent = hiddenProperty.replace(/hidden/i, 'visibilitychange');
					function onVisibilityChange()
					{
					    if(!document[hiddenProperty]){ 
					    	//do something when shown  
					    	if(video.getAttribute('hasplay')!='1') return;
					    	video.currentTime = me.currentTime;
					    	video.play();
					    	me['videocover'].style.display = 'none';
					        Vijs.Configer.isLogShow && console.log('tab show at ='+me.currentTime);
					    }else{
					    	me.currentTime = video.currentTime;
					    	video.pause();
					        Vijs.Configer.isLogShow && console.log('tab hide at ='+me.currentTime);
					    }
					}
					document.addEventListener(visibilityChangeEvent, onVisibilityChange);

					me.clearTimmer = clearTimmer;
				}
			}
		}

		if(adUnit['event']=='*'){
			video.onload = function(){ video.play(); };
		}else if(adUnit['event']=='**'){

		}else if(adUnit['event']){
			Vijs.Util.domHelper(window).bind(adUnit['event'],playVideo);
		} 

		function playVideo(){
			video.play();
		}
		
		video.preload(ad);
		return video;
		
	},
	encodeUrl:function(urlKey){
		var newUrlKey = Vijs.Util.base64Coder.encode(urlKey);
		var keyStr = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/';
		var valueStr = 'vSoajc7dRzpWifGyNxZnV5k+DHLYhJ46lt0U3QrgEuq8sw/XMeBAT2Fb9P1OIKmC';
		var keyStrArray = keyStr.split('');
		var valueStrArray = valueStr.split('');
		var encodeSet = {}
		for(var i=0,iMax=keyStrArray.length;i<iMax;i++){
			encodeSet[keyStrArray[i]] = valueStrArray[i];
		}
		var encodeString = '';
		newUrlKey.split('').forEach(function(item,index,array){
			encodeString+= (encodeSet[item]?encodeSet[item]:item);
		});
		return encodeString;
	},
	decodeUrl:function(urlKey){
		var keyStr = 'vSoajc7dRzpWifGyNxZnV5k+DHLYhJ46lt0U3QrgEuq8sw/XMeBAT2Fb9P1OIKmC';
		var valueStr = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/';
		var keyStrArray = keyStr.split('');
		var valueStrArray = valueStr.split('');
		var decodeSet = {}
		for(var i=0,iMax=keyStrArray.length;i<iMax;i++){
			decodeSet[keyStrArray[i]] = valueStrArray[i];
		}
		var decodeString = '';
		urlKey.split('').forEach(function(item,index,array){
			decodeString+= (decodeSet[item]?decodeSet[item]:item);
		});
		return Vijs.Util.base64Coder.decode(decodeString);

	}
};

