var Vijs = Vijs || {};

Vijs.init = Vijs.init || function(mvrvtag){
	Vijs.Configer.preInit(mvrvtag);
	Vijs.Util.addListener(document,'DOMContentLoaded',function(){
		var ua = window.navigator.userAgent.toLowerCase();
		if(ua.indexOf('googlebot')>=0||ua.indexOf('search console')>=0) return;
		Vijs.Logger.init();
		Vijs.Configer.init(mvrvtag,function(result){
			if(result) Vijs.Offer.init(mvrvtag);
		});

		var gct = Vijs.Cache.getLocalStorage('gct');//get cookie time
		var isSendGCT = gct ? ((new Date()).getTime()-parseInt(gct)>86400000?true:false):true;
		if(isSendGCT){
			Vijs.Logger.post({'eventType':'cookieInfo'});
			Vijs.Cache.setLocalStorage('gct',(new Date()).getTime());
		}
	});

	
}

Vijs.setAD = Vijs.setAD || function(adConfig){
	var adUnit = Vijs.Offer.addAdUnit(adConfig);
	var adObj = {
		'unitid':adConfig['unitid'],
		show:function(){
			adUnit['videoPlayer'].play();
		},
		close:function(){
			adUnit['videoPlayer'].close();
			adUnit['endcardPlayer'].close();
		},
		pause:function(){
			adUnit['videoPlayer'].pause();
		},
		checkIsLoadComplete:function(){
			return adUnit['loadComplete'];
		}
	}
	return adObj;
};


