var Vijs = Vijs || {};
Vijs.Logger = {
	debug: true,
	uploadLog: true,
	postType:'MV',
	init: function() {

		this['debug'] = (Vijs.Util.getQueryString(window.location.href, 'mvLogDebug') == 'true') ? true : (Vijs.Configer['isLogShow'] || false);
		// if(this.debug==false) console.log = function(){};
	},
	post: function(param) {
		var me = this;
		if (this.uploadLog == true) {
			var postObj = me['loggerSet'][param['postType']]? me['loggerSet'][param['postType']] : me['loggerSet'][me['postType']];
			postObj.doPost(param);
		}
	},
	loggerSet: {
		'GA': {
			hasInit: false,
			checkEnable: true,
			GAId: 'UA-62639739-1',
			_loadGA: function(callback) {
				var me = this;
				(function(i, s, o, g, r, a, m) {
					i['GoogleAnalyticsObject'] = r;
					i[r] = i[r] || function() {
						(i[r].q = i[r].q || []).push(arguments)
					}, i[r].l = 1 * new Date();
					a = s.createElement(o),
						m = s.getElementsByTagName(o)[0];
					a.async = 1;
					a.src = g;
					m.parentNode.insertBefore(a, m)
				})(window, document, 'script', '//www.google-analytics.com/analytics.js', 'ga');

				ga('create', me.GAId, 'auto');
				ga('send', 'pageview');
				me.hasInit = true;
				if (callback) callback();
			},
			doPost: function(param) {
				var me = this;
				if (me.hasInit == true) {
					me._doPost(param);
				} else {
					me._loadGA(function() {
						me._doPost(param);
					});
				}
			},
			_doPost: function(param) {
				var me = this;
				var eventValue = parseInt(param['eventValue']);
				if(isNaN(eventValue)){
					ga('send', 'event', 'JSSDK_'+param['eventType'], param['eventDesc'], eventValue);
				}else{
					ga('send', 'event', 'JSSDK_'+param['eventType'], param['eventDesc']);
				}
			}
		},
		'MV': {
			doPost:function(param){
				var me = this;
				var eventType = param['eventType'];
				switch(eventType){
					case 'videoStuck':
						me._videoStuck(param);
						break;
					case 'cookieInfo':
						me._cookieInfo(param);
						break;
					case 'videoClose':
						me._videoClose(param);
						break;
					case 'videoEnd':
						me._videoEnd(param);
						break;
					case 'videoLoadSuccess':
						me._videoLoadSuccess(param);
						break;
					case 'videoLoadFail':
						me._videoLoadFail(param);
						break;
					default:
						if(param['url']) me._postToUrl(param);
				}
			},
			_videoStuck:function(param){
				var postData = {
					'key':'5000047',
					'cid': param['offer_id'],
					'view_length':param['view_length'],//观看长度
					'video_length':param['video_length'],
					'rid':Vijs.Util.getQueryString(param['notice_url'],'k'),
					'unit_id':param['unit_id'],
					'network_type':Vijs.Configer['os']['network_type'],
					'browser':Vijs.Configer['os']['browser'],
					'browser_os':Vijs.Configer['os']['browser_version'],
					'ratio':Vijs.Configer['os']['ratio'],
					'platform':Vijs.Configer['os']['platform']
				};
				this._postToServer(postData);
			},
			_cookieInfo:function(param){
				var postData = {
					'key':'5000048',
					'browser':Vijs.Configer['os']['browser'],
					'domain_url':Vijs.Configer['os']['domain'],
					'ua':Vijs.Configer['os']['ua'],
					'cookie':document.cookie,
					'platform':Vijs.Configer['os']['platform']
				};
				this._postToServer(postData);
			},
			_videoClose:function(param){
				var postData = {
					'key':'5000049',
					'cid': param['offer_id'],
					'offer_url': param['notice_url'],
					'unit_id':param['unit_id'],
					'view_length':param['view_length'],//观看长度
					'video_length':param['video_length'],
					'reason':param['reason'],//1.skip/2.break
					'platform':Vijs.Configer['os']['platform']
				};
				this._postToServer(postData);
			},
			_videoEnd:function(param){
				var postData = {
					'key':'5000050',
					'cid': param['offer_id'],
					'rid':Vijs.Util.getQueryString(param['notice_url'],'k'),
					'unit_id':param['unit_id'],
					'ratio':Vijs.Configer['os']['ratio'],
					'browser':Vijs.Configer['os']['browser'],
					'browser_os':Vijs.Configer['os']['browser_version'],
					'network_type':Vijs.Configer['os']['network_type'],
					'video_length':param['video_length'],
					'platform':Vijs.Configer['os']['platform']
				};
				this._postToServer(postData);
			},
			_videoLoadSuccess:function(param){
				var postData = {
					'key':'5000051',
					'cid': param['offer_id'],
					'duration':param['duration'],
					'network_type':Vijs.Configer['os']['network_type'],
					'video_size':param['video_size'],
					'image_size':param['image_size'],
					'video_length':param['video_length'],
					'browser':Vijs.Configer['os']['browser'],
					'browser_os':Vijs.Configer['os']['browser_version'],
					'platform':Vijs.Configer['os']['platform']
				};
				this._postToServer(postData);
			},
			_videoLoadFail:function(param){
				var postData = {
					'key':'5000052',
					'cid': param['offer_id'],
					'network_type':Vijs.Configer['os']['network_type'],
					'image_url':param['image_url'],
					'video_url':param['video_url'],
					'reason':param['reason'],
					'rid':Vijs.Util.getQueryString(param['notice_url'],'k'),
					'unit_id':param['unit_id'],
					'platform':Vijs.Configer['os']['platform']
				};
				this._postToServer(postData);
			},
			_postToServer:function(postData){
				var logserverurl = Vijs.Configer.getUrl('logserverurl')+'?ts='+(new Date()).getTime();
				for(var key in postData){
					logserverurl += ('&' + key + '=' + postData[key]);
				}
				var poster = Vijs.Util.domHelper('img').css({
		          'position': 'absolute',
		          'z-index': 0,
		          'top': '-1px',
		          'left': '-1px',
		          'width': '1px',
		          'height': '1px',
		          'opacity':0.001,
		          'display':'block'
		        }).attr('src',logserverurl).insertTo(document.body);
		        setTimeout(function(){ poster.remove() },1000);
			},
			_postToUrl:function(postData){
				var logserverurl = postData['url']+'?ts='+(new Date()).getTime();
				for(var key in postData){
					if(key=='url') continue;
					logserverurl += ('&' + key + '=' + postData[key]);
				}
				var poster = Vijs.Util.domHelper('img').css({
		          'position': 'absolute',
		          'z-index': 0,
		          'top': '-1px',
		          'left': '-1px',
		          'width': '1px',
		          'height': '1px',
		          'opacity':0.001,
		          'display':'block'
		        }).attr('src',logserverurl).insertTo(document.body);
		        setTimeout(function(){ poster.remove() },1000);
			}
		}
	}
};

