var Vijs = Vijs || {};
Vijs.Render = {
  'renderMode': 'template_receive_data',
  'viewType': {
    '94': {
      'name': 'videoendcard',
      'offerQuantity': 1,
      'size': {
        'width': 100,
        'height': 100,
        'dimension': 'scale',
        'sizeType': 7
      },
      'viewBackgroundColor': 'transparent',
      'url': 'template/full_screen.html',
      'devUrl': 'template/full_screen.dev.html',
      initFrame: function (adUnit, adUnitData) {
        if (!adUnitData) return;
        var me = this;

        var path = Vijs.Render.getUrl(me['url'], me['devUrl']);
        var dataString = JSON.stringify(adUnitData);
        var dataUrlString = '?data=' + encodeURIComponent(encodeURIComponent(dataString)) + '&ts=' + Math.floor(new Date().getTime() / 60000);

        var closeable = adUnit.el.attr('closeable') || 'false';
        var viewStyle = adUnit.el.attr('style');
        var position = adUnit.el.attr('position') || (Vijs.Configer['os']['browserType'] == 'mobile' ? 'bottom' : 'inline');
        var frameWarpper = Vijs.Util.domHelper('div');
        var frame = Vijs.Util.domHelper('iframe');

        frameWarpper.css({
          'position': 'fixed',
          'z-index': 2147483646,
          'top': '0px',
          'left': '0px',
          'right': '0px',
          'bottom': '0px'
        });
        var frameWidth = Vijs.Configer['currentScreen']['width'];
        var frameHeight = Vijs.Configer['currentScreen']['height'];
        var templateUrl = Vijs.Configer['isDevTemplate'] ? path['devUrl'] : path['url'];
        frame.css({
          'width': '100%',
          'height': '100%',
          'margin': '0',
          'padding': '0',
          'border': 'none'
        }).insertTo(frameWarpper).attr('src', templateUrl + dataUrlString);
        Vijs.Util.replaceEach(frameWarpper.el, adUnit['el'].el);
        if (true) {//fullscreen must have close button
          var frameWarpperSize = frameWarpper.size();
          var closeWidth = Math.floor(frameWarpperSize['width'] / 15);
          Vijs.Render.addCloseButton(frameWarpper, closeWidth);
        }
      }

    }
  },
  getUrl: function (url, devUrl) {
    return (Vijs.Configer['host']) ? {
      'url': (Vijs.Configer['host'] + url),
      'devUrl': (Vijs.Configer['host'] + devUrl)
    } : {
      'url': url,
      'devUrl': devUrl
    };
  },
  addCountingCloseButton: function(frameWarpper,count, closeWidth, position,callback) {
    if(count==-100) return;
    var me = this;
    var countNumber = count;
    var closeButtonBgColor = 'rgba(255,255,255,0.3)';
    var closeButtonBorderColor = 'rgba(255,255,255,0.3)';
    var closeButtonCrossColor = 'rgba(255,255,255,1)';
    var position = position || 'righttop';
    var topPosition = position.indexOf('top') >= 0 ? 'top' : 'bottom';
    var leftPosition = position.indexOf('left') >= 0 ? 'left' : 'right';
    var closeHalfWidth = Math.floor(closeWidth / 2);
    var closeMargin = Math.floor(closeWidth / 3);
    var closeEl = Vijs.Util.domHelper('div').css({
      'display': 'block',
      'width': closeWidth + 'px',
      'height': closeWidth + 'px',
      'line-height': closeWidth + 'px',
      'text-align': 'center',
      'border': '3px solid ' + closeButtonBorderColor,
      'border-radius': closeHalfWidth + 'px',
      'position': 'absolute',
      'z-index': 2147483647,
      'font-family': 'arial',
      'font-size': closeWidth + 'px',
      'color': closeButtonCrossColor,
      'background-color': closeButtonBgColor
    }).css(topPosition, (closeMargin + 'px'))
      .css(leftPosition, (closeMargin + 'px')).insertTo(frameWarpper)
      

    if(count<=0){
      delete closeEl;
      me.addCloseButton(frameWarpper, closeWidth, position,callback);
      return;
    }else{
      closeEl.html(countNumber);
    }
    var countTimmer = window.setInterval(function(){
      countNumber--;
      if(countNumber>0){
        closeEl.html(countNumber);
      }else{
        closeEl.remove();
        me.addCloseButton(frameWarpper, closeWidth, position,callback);
        window.clearTimeout(countTimmer);
      }
    },1000);
    return function(){
      closeEl.remove();
      me.addCloseButton(frameWarpper, closeWidth, position,callback);
      window.clearTimeout(countTimmer);
    };
  },
  addCloseButton: function (frameWarpper, closeWidth, position,callback) {
    var closeButtonBgColor = 'rgba(255,255,255,0.3)';
    var closeButtonBorderColor = 'rgba(255,255,255,0.3)';
    var closeButtonCrossColor = 'rgba(255,255,255,1)';
    var position = position || 'righttop';
    var topPosition = position.indexOf('top') >= 0 ? 'top' : 'bottom';
    var leftPosition = position.indexOf('left') >= 0 ? 'left' : 'right';
    var closeHalfWidth = Math.floor(closeWidth / 2);
    var closeMargin = Math.floor(closeWidth / 3);
    var closeEl = Vijs.Util.domHelper('div').css({
      'display': 'block',
      'width': closeWidth + 'px',
      'height': closeWidth + 'px',
      'line-height': closeWidth + 'px',
      'text-align': 'center',
      'border': '3px solid ' + closeButtonBorderColor,
      'border-radius': closeHalfWidth + 'px',
      'position': 'absolute',
      'z-index': 2147483647,
      'font-family': 'arial',
      'font-size': closeWidth + 'px',
      'color': closeButtonCrossColor,
      'background-color': closeButtonBgColor
    }).css(topPosition, (closeMargin + 'px'))
      .css(leftPosition, (closeMargin + 'px')).insertTo(frameWarpper)
      .bind('click', function (e) {
        var parent = Vijs.Util.domHelper(this.parentNode);
        if (parent.attr('jump')) return;
        parent.attr('jump', true);
        parent.remove();
        if(callback) callback();
        Vijs.Util.cancelBubble(e);
      })
      .bind('touchend', function (e) {
        var parent = Vijs.Util.domHelper(this.parentNode);
        if (parent.attr('jump')) return;
        parent.css('opacity', '0.001').attr('jump', true);
        window.setTimeout(function () {
          parent.remove();
          if(callback) callback();
        }, 500);
        Vijs.Util.cancelBubble(e);
      }).html(decodeURIComponent('%C3%97'));
  },
  'template': {
    init: function (resultData, renderAction) {
      //console.log(Vijs.Render['renderMode']);
      switch (Vijs.Render['renderMode']) {
        case 'template_receive_data':
          //console.log(2)
          var dataString = Vijs.Util.getQueryString(window.location.href, 'data');
          var resultData = JSON.parse(decodeURIComponent(dataString));
          renderAction(resultData);
          break;
        case 'template_request_data':
          break;
        case 'template_within_data':
          renderAction(resultData);
          Vijs.Offer.preClickOffer(resultData);
          Vijs.Offer.setNoticeImpression(resultData);
          break;
        case 'local_request_data':

          break;
      }
    }
  },
  'renderAction': {
    template: function (adUnitData, adUnit, callback) {
      //to do

      if (callback) callback();
    },
    local: function (adUnitData, adUnit, callback) {
    }
  },
  renderOffer: function (adUnitData, adUnit, callback) {
    Vijs.Configer.isLogShow && console.log(adUnit.el.el);
    var me = this;
    var renderMode = me['renderMode'];
    // Vijs.Configer.isLogShow && console.log(adUnitData);
    var viewType = adUnit['viewType'];
    var cloneAdUnitData = adUnitData ? JSON.parse(JSON.stringify(adUnitData)) : null;
    if (cloneAdUnitData && cloneAdUnitData['data'] && cloneAdUnitData['data']['only_impression_url']) {
      delete cloneAdUnitData['cache_time'];
      delete cloneAdUnitData['msg'];
      delete cloneAdUnitData['data']['only_impression_url'];
      delete cloneAdUnitData['data']['parent_session_id'];
      delete cloneAdUnitData['data']['session_id'];
      delete cloneAdUnitData['data']['unit_size'];

      //for zipping the data to the template
      if (cloneAdUnitData['data']['ads']) {
        var templateOfferCapacity = me.viewType[adUnit.viewType]['offerQuantity'];//maybe some template can show offer more than 1;
        for (var i = cloneAdUnitData['data']['ads'].length - 1; i >= 0; i--) {
          if ((i + 1) > templateOfferCapacity) {
            cloneAdUnitData['data']['ads'].pop();
            continue;
          }
          delete cloneAdUnitData['data']['ads'][i]['ad_source_id'];
          delete cloneAdUnitData['data']['ads'][i]['c_ct'];
          delete cloneAdUnitData['data']['ads'][i]['click_mode'];
          delete cloneAdUnitData['data']['ads'][i]['fca'];
          delete cloneAdUnitData['data']['ads'][i]['fcb'];
          delete cloneAdUnitData['data']['ads'][i]['ttc'];
          delete cloneAdUnitData['data']['ads'][i]['ttc_ct'];
          delete cloneAdUnitData['data']['ads'][i]['impression_url'];
        }

      }
    }
    me['viewType'][viewType].initFrame(adUnit, cloneAdUnitData);
    if (cloneAdUnitData && callback) callback();
  },
  takeAction: function (btn, click_url, action, result) {
    if (!btn.attr('notice')) {
      btn.attr('notice', true);
      setTimeout(function () {
        btn['el'].removeAttribute('notice');
      }, 500);
      Vijs.Offer.setNotice(result);
    }

    if (!btn.attr('jump')) {
      btn.attr('jump', true);
      setTimeout(function () {
        btn['el'].removeAttribute('jump');
      }, 500);
      btn.attr('href', click_url);//.trigger(action);
    }
  }
};
