var Vijs = Vijs || {};
Vijs.Offer = {
  'adUnitArray': [],
  'adOffset': 0,
  'fcaTime': 86400,
  'defaultAdUnitSetting': {
    'status': 1,
    'data': {
      'getpf': 1,//配置信息缓存时间，单位是秒
      'aqn': 1,//广告请求条数
      'acn': 1,//广告缓存条数
      'plct': 3600,//广告缓存时间，单位是秒
      'ttct': 1,//预点击触发时机。1表示展示时做预点击，2表示请求广告返回时做预点击。
      'pcrn': 100 // TTC ID中campaign id的最大数量限制，默认为100
    },
    'cache_time': 0
  },
  init: function (mvrvtag) {
    var adUnit = this.setAdUnit(mvrvtag);
    return this.getOffer(adUnit);
  },
  addAdUnit:function(config){
    var me = this;
    var unitEl = Vijs.Util.domHelper('script');
    unitEl.attr('fetch', '1').attr('unitid',config['unitid']).attr('view','94');
    if(config['android_id']) unitEl.attr('android_id',config['android_id']);
    if(config['imei']) unitEl.attr('imei',config['imei']);
    if(config['gaid']) unitEl.attr('gaid',config['gaid']);
    if(config['deviceid']) unitEl.attr('deviceid',config['deviceid']);
    var adUnit = this.setAdUnit(unitEl);
    adUnit['rewardedCallback'] = config['rewardedCallback'];
    adUnit['loadedCallback'] = config['loadedCallback'];
    adUnit['event'] = '**';

    var ad = me.getOffer(adUnit);

    return ad;
  },
  setAdUnit:function(mvrvtag){
    var me = this;
    var unitEl = Vijs.Util.domHelper(mvrvtag);
    Vijs.Configer.isLogShow && console.log(adUnitEls[i]);
    var unitid = unitEl.attr('fetch', '1').attr('unitid');
    var view = unitEl.attr('view') || '94';
    view = Vijs.Render['viewType'][view] ? view : 94;
    var unitSize = Vijs.Render['viewType'][view]['size']['sizeType'];
    var unitIndex = me['adUnitArray'].length;
    var adUnit = {
      'appid': Vijs.Configer['publisher']['appid'],
      'apikey': Vijs.Configer['publisher']['apikey'],
      'adtype':94,
      'sign': Vijs.Configer['publisher']['sign'],
      'unitid': unitid,
      'image_size': unitSize,
      'viewType': view,
      'unitofferkey': 'mvadunitoffer' + Vijs.Configer['publisher']['sign'] + unitid + 'elid' + unitIndex,
      'unitsettingkey': 'mvadunitsetting' + Vijs.Configer['publisher']['sign'] + unitid + 'elid' + unitIndex,
      'offerfcKey': 'mvadunitfc' + Vijs.Configer['publisher']['sign'] + unitid + 'elid' + unitIndex,
      'vabofferKey': 'vabofferKey' + Vijs.Configer['publisher']['sign'] + unitid + 'elid' + unitIndex,
      'adCount': 1,
      'el': unitEl,
      'resourceArray':{},
      'videoLoadComplete':false,
      'endcardLoadComplete':false,
      'loadComplete':false,
      'android_id':unitEl.attr('android_id') || '',
      'imei':unitEl.attr('imei') || '',
      'gaid':unitEl.attr('gaid') || '',
      'deviceid':unitEl.attr('deviceid') || ''
    };

    var triggerEvent = unitEl.attr('event');
    adUnit['event'] =  triggerEvent? triggerEvent : '*';
    adUnit['setting'] = me.getAdUnitSetting(adUnit);
    adUnit['adCount'] = (adUnit['setting']['data']['aqn'] && parseInt(adUnit['setting']['data']['aqn']) > 0) ? parseInt(adUnit['setting']['data']['aqn']) : 1;
    adUnit['adCount'] = adUnit['adCount'] >= Vijs.Render.viewType[view]['offerQuantity'] ? adUnit['adCount'] : Vijs.Render.viewType[view]['offerQuantity'];
    // tnum is real show number
    adUnit['tnum'] = (adUnit['setting']['data']['acn'] && parseInt(adUnit['setting']['data']['acn']) > 0) ? parseInt(adUnit['setting']['data']['acn']) : 1;
    // limit
    adUnit['pcrn'] = (adUnit['setting']['data']['pcrn'] && parseInt(adUnit['setting']['data']['pcrn']) > 0) ? parseInt(adUnit['setting']['data']['pcrn']) : 100;
    adUnit['offerFcSet'] = me.getOfferFcSet(adUnit);
    // add vba array
    adUnit['aVbaAds'] = me.getVbaOffer(adUnit);
    me['adUnitArray'].push(adUnit);
    return adUnit;
  },
  pullUsedOffer: function (adUnitData, adUnit, every) {
    // render && delete
    var offerQuantity = Vijs.Render.viewType[adUnit.viewType]['offerQuantity'];
    for (var i = 0; i < offerQuantity; i++) {
      if (adUnitData.data.ads.length > 0) {
        if (every) every();
        adUnitData.data.ads.shift();
      }
    }
    return adUnitData.data.ads.length >= offerQuantity ? adUnitData : null;
  },
  preloadOfferResource:function(adUnit,adUnitData,callback){
    var me = this;
    Vijs.Configer.isLogShow && console.log('preloadOfferResource');
    adUnit['videoPlayer'] = null;
    adUnit['endcardPlayer'] = null;
    adUnit['reload'] = function(){ 
      adUnit['videoLoadComplete'] = false;
      adUnit['endcardLoadComplete'] = false;
      adUnit['loadComplete'] = false;
      adUnit['hasInit']=false; 
      me.getOffer(adUnit); 
    };
    adUnit['videoPlayer'] = Vijs.videoPlayer.init(adUnit,adUnitData,function(){
      adUnit['videoLoadComplete'] = true;
      adUnit['endcardPlayer'] = Vijs.endcardPlayer.init(adUnit,adUnitData,function(){
        adUnit['endcardLoadComplete'] = true;
        adUnit['loadComplete'] = true;
        if(adUnit.loadedCallback) adUnit.loadedCallback();
        if(callback) callback();
      });
    });
  },
  getOffer: function (adUnit) {
    var me = this;
    var unitOfferArray = [];
    unitOfferArray = adUnit ? [adUnit] : me['adUnitArray'];
    for (var i = 0, iMax = unitOfferArray.length; i < iMax; i++) {
      var unitObj = unitOfferArray[i];
      if (unitObj['hasInit'] === true) continue;
      unitObj['hasInit'] = true;
      (function (adUnit) {
        adUnit['platform'] = Vijs.Configer['os']['platform'];
        adUnit['os_version'] = Vijs.Configer['os']['os_version'];
        adUnit['offset'] = me['adOffset'];
        var adUnitData = me.loadPreOffer(adUnit);
        if (adUnitData) {
          Vijs.Configer.isLogShow && console.log('cache offer loaded');
          Vijs.Offer.preloadOfferResource(adUnit,adUnitData['data'],function(){

          });

          // Vijs.Render.renderOffer(adUnitData, adUnit, function () {
          //   if (adUnit['setting']['data']['ttct'] == 1) {
          //     me.preClickOffer(adUnitData, adUnit);
          //   }
          //   var offerData = me.pullUsedOffer(adUnitData, adUnit, function () {
          //     me.pushOfferFc(adUnitData, adUnit);
          //     me.setImpression(adUnitData);
          //   });
          //   me.cachePreOffer(adUnit, offerData);
          //   me.setNoticeImpression(adUnitData);
          // });
        } else {
          me['adOffset'] += adUnit['adCount'];//only if the offer not come from cache, then the offset for request will be valueable
          Vijs.Configer.isLogShow && console.log('cache offer is empty');
          me.loadRTOffer(adUnit, function (result) {
            // console.log(JSON.stringify(result))
            Vijs.Offer.preloadOfferResource(adUnit,result['data'],function(){ });
            return;
            // Vijs.Render.renderOffer(result, adUnit, function () {
            //   if (adUnit['setting']['data']['ttct'] == 1) {
            //     me.preClickOffer(result, adUnit);
            //   }
            //   var offerData = me.pullUsedOffer(result, adUnit, function () {
            //     me.pushOfferFc(result, adUnit);
            //     me.setImpression(result);
            //   });

            //   adUnit['setting'] = result['data']['setting']||me['defaultAdUnitSetting'];
            //   me.cachePreOffer(adUnit, offerData);
            //   me.setNoticeImpression(result);
            // });

            // if (result && adUnit['setting']['data']['ttct'] == 2) {
            //   me.preClickOffer(result, adUnit);
            // }
          })
        }
      })(unitObj);

    }
    if(adUnit) return adUnit;
  },
  loadRTOffer: function (adUnit, callback) {
    var me = this;
    if (Vijs.Configer['os']['browserType'] != 'mobile'&&(!Vijs.Configer['forceMobileUser'])) {
      var failResult = {"status": -1, "msg": "EXCEPTION_RETURN_EMPTY"};
      if (callback) {
        try {
          callback(null);
        } catch (ex) {
          Vijs.Configer.isLogShow && console.log(ex);
        }
      }
      Vijs.Logger.post({
        'eventType': 'v3_stop_notTargetUser',
        'eventDesc': Vijs.Configer['os']['browserType']
      });
      return;
    }

    var url = me.getUrl(adUnit);
    Vijs.Util.mobvistaAjax({
      url: url,
      type: 'get',
      dataType: 'json',
      contentType: 'json',
      success: function (result) {
        if (result['status'] == 1) {
          Vijs.Configer.isLogShow && console.log('new offer load');
          if (callback) {
            try {
              callback(result);
            } catch (ex) {
              Vijs.Configer.isLogShow && console.log(ex);
            }
          }
        } else {
          if (callback) {
            try {
              callback(null);
            } catch (ex) {
              Vijs.Configer.isLogShow && console.log(ex);
            }
          }
          Vijs.Logger.post({
            'eventType': 'v3_return_nosuccess_' + result['msg'],
            'eventDesc': url
          });
        }
        Vijs.Configer.isLogShow && console.log(result);
      },
      error: function (result) {
        Vijs.Configer.isLogShow && console.log(result);
        if (callback) {
          try {
            callback(null);
          } catch (ex) {
            Vijs.Configer.isLogShow && console.log(ex);
          }
        }
        Vijs.Logger.post({
          'eventType': 'v3_error',
          'eventDesc': url
        });
      }
    });
  },
  loadPreOffer: function (adUnit) {
    var me = this;
    var adunitofferkey = adUnit['unitofferkey'];
    var result = Vijs.Cache.getLocalStorage(adunitofferkey);
    if (!result) return null;
    var cacheDateDiff = Vijs.Util.dateDiff(result['cache_time'], (new Date()), 'second');
    if (Vijs.Render
      && result['data']['ads'].length >= Vijs.Render.viewType[adUnit.viewType]['offerQuantity']
      && cacheDateDiff < adUnit['setting']['data']['plct']) {
      return result;
    } else {
      return null;
    }
  },
  cachePreOffer: function (adUnit, offerData) {
    var me = this;
    var adunitofferkey = adUnit['unitofferkey'];
    if (Vijs.Configer['os']['browserType'] != 'mobile') {
      if (Vijs.Render.viewType[adUnit.viewType]['backupUrl']) {
        Vijs.Cache.setLocalStorage(adunitofferkey, null);
      }
      Vijs.Logger.post({
        'eventType': 'v3_stop_notTargetUser',
        'eventDesc': Vijs.Configer['os']['browserType']
      });
      return;
    }
    var cacheTime = (new Date()).getTime();
    if (offerData) {
      offerData['cache_time'] = cacheTime;
      Vijs.Cache.setLocalStorage(adunitofferkey, offerData);
      return;
    }
    adUnit['offset'] = me['adOffset'];
    var url = me.getUrl(adUnit);
    Vijs.Util.mobvistaAjax({
      url: url,
      type: 'get',
      dataType: 'json',
      contentType: 'json',
      success: function (result) {
        Vijs.Configer.isLogShow && console.log('new offer cache');
        if (result['msg'] == 'success') {
          result['cache_time'] = cacheTime;
          var preClickTime = false;
          if (adUnit['setting']['data']['ttct'] == 2) {
            preClickTime = me.preClickOffer(result, adUnit);
          }
          if (preClickTime != false) {
            result['pre_click_time'] = preClickTime;
          }
          var acn = parseInt(adUnit['setting']['data']['acn']);
          if (acn > 0) {
            for (var i = (result['data']['ads'].length - 1); i >= (acn); i--) {
              result['data']['ads'].pop();
            }
          }
          Vijs.Cache.setLocalStorage(adunitofferkey, result);
        } else {
          if (Vijs.Render.viewType[adUnit.viewType]['backupUrl']) Vijs.Cache.setLocalStorage(adunitofferkey, null);
          Vijs.Logger.post({
            'eventType': 'v3_cache_return_nosuccess_' + result['msg'],
            'eventDesc': url
          });
        }
      },
      error: function (result) {
        Vijs.Configer.isLogShow && console.log(result);

        Vijs.Logger.post({
          'eventType': 'v3_cache_error',
          'eventDesc': url
        });
      }
    });
    me['adOffset'] += adUnit['adCount'];
  },
  getAdUnitSetting: function (adUnit) {
    var setting = this.loadPreSetting(adUnit);
    if (!setting) setting = this['defaultAdUnitSetting'],
    Vijs.Configer.isLogShow && console.log('set adunit ' + adUnit['unitid'] + ' in default setting');
    return setting;
  },
  loadPreSetting: function (adUnit) {
    var me = this;
    var adunitsettingkey = adUnit['unitsettingkey'];
    var setting = Vijs.Cache.getLocalStorage(adunitsettingkey);
    if (setting) {
      Vijs.Configer.isLogShow && console.log('adunit ' + adUnit['unitid'] + '\'s setting is loaded');
    } else {
      Vijs.Logger.post({
        'eventType': 'jssetting_null',
        'eventDesc': adunitsettingkey
      });
    }
    return setting;
  },
  getUrl: function (param) {
    Vijs.Configer.isLogShow && console.log(param)
    var exclude_ids = this.getOfferFcIds(param);
    var ttc_ids = this.getOfferTtcIds(param);
    var url = Vijs.Configer.getUrl('offerurl') + '?only_impression=1&'
      + 'app_id=' + param['appid']
      + '&unit_id=' + param['unitid'] + '&ping_mode=1'
      + '&sign=' + param['sign']
      + '&content_type=' + param['adtype']
      + '&network_type=' + Vijs.Configer['os']['network_type']
      + '&main_domain=' + Vijs.Configer['os']['domain']
      + '&screen_size=' + Vijs.Configer['os']['screen_size']
      + '&platform=' + param['platform']
      + '&orientation=' + (window.innerWidth>window.innerHeight? 2:1)
      + '&ua=' + navigator.userAgent
      + '&ad_num=' + param['adCount']
      + '&offset=' + param['offset']
      + '&os_version=' + param['os_version']
      + '&sdk_version=' + Vijs.Configer['version']
      + '&image_size=' + param['image_size']
      + '&android_id=' + param['android_id']
      + '&imei=' + param['imei']
      + '&gaid=' + param['gaid']
      + '&deviceid=' + param['deviceid']
      + '&version_flag=1'
      // + '&exclude_ids=' + exclude_ids
      + '&tnum=' + param['tnum']  // 用于真正展示的广告数量
      + '&ttc_ids=' + ttc_ids; // 已做过VBA的offer信息
    url += ('&ts=' + (new Date()).getTime());
    // url += '&client_ip=54.12.1.13';

    return url;
  },
  preClickOffer: function (offerResult, adUnit) {
    var now = (new Date).getTime();
    var ads = offerResult['data']['ads'];
    var uaString = window.navigator.userAgent.toLowerCase();
    var aTmp = [];
    for (var i = 0, iMax = ads.length; i < iMax; i++) {
      if (ads[i]['ttc'] == false) continue;
      if (offerResult['pre_click_time'] && Vijs.Util.dateDiff(offerResult['pre_click_time'], now, 'second') < ads[i]['ttc_ct']) continue;
      var oTmp = {};
      oTmp.id = ads[i].id;
      oTmp.click_url = ads[i].click_url;
      oTmp.ts = (new Date).getTime();
      aTmp.push(oTmp);
    }
    var aVba = this.diffVbaOffer(aTmp, adUnit);
    if (!aVba.length) return now;
    // cache vba offer
    this.cacheVbaOffer(aVba, adUnit);
    for (var i = 0; i < aVba.length; i++) {
      var click_url = aVba[i]['click_url'];
      var silentTag = (uaString.indexOf('android') > 0 && uaString.indexOf('uc') > 0) ? 'script' : 'img';
      this.loadLinkInSilent(click_url, silentTag, true);
    }
    return now;
  },
  setImpression: function (offerResult) {
    var only_impression_url = offerResult['data']['ads'][0]['impression_url'];
    this.loadLinkInSilent(only_impression_url);
  },
  setNoticeImpression: function (offerResult) {
    var only_impression_url = offerResult['data']['only_impression_url'];
    this.loadLinkInSilent(only_impression_url);
  },
  setNotice: function (offerResult) {
    var notice_url = offerResult['data']['ads'][0]['notice_url'];
    this.loadLinkInSilent(notice_url);
  },
  loadLinkInSilent: function (url, silentTag, isAgent) {
    //console.log(isAgent);
    var silentTag = silentTag || 'script';
    var silentDataUrlString = url;
    var targetTag = silentTag;
    if (isAgent) {
      targetTag = 'iframe';
      var silentData = {
        'url': url,
        'silentTag': silentTag
      };
      var silentDataString = JSON.stringify(silentData);
      silentDataUrlString = Vijs.Configer.getUrl('silenturl') + '?data=' + encodeURIComponent(encodeURIComponent(silentDataString));
      //silentDataUrlString = 'http://localhost:63342/mv/trunk/sender/slp.html' + '?data=' + encodeURIComponent(encodeURIComponent(silentDataString));
    }

    var urlEl = Vijs.Util.domHelper(targetTag).css({
      'position': 'absolute',
      'left': '-9999px',
      'top': '-9999px',
      'width': '1px',
      'height': '1px',
      'opacity': '0.001',
      //'z-index': '-9999',
      'background-color': 'white'
    }).attr({
      'src': silentDataUrlString
    });
    urlEl.insertTo(document.body);
  },
  pushOfferFc: function (offerResult, adUnit) {
    //console.log(offerResult, adUnit);
    var offerFcSet = adUnit['offerFcSet'] || {};
    var offerId = offerResult['data']['ads'][0]['id'];
    var now = (new Date()).getTime();
    var fca = offerResult['data']['ads'][0]['fca'];
    offerFcSet[offerId] = offerFcSet[offerId] || {
        'fcaArray': []
      };
    offerFcSet[offerId]['fcaTime'] = this['fcaTime'];
    offerFcSet[offerId]['fca'] = fca;
    while (offerFcSet[offerId]['fcaArray'].length > (fca - 1) && offerFcSet[offerId]['fcaArray'].length > 0) {
      offerFcSet[offerId]['fcaArray'].shift();
    }
    offerFcSet[offerId]['fcaArray'].push(now);
    adUnit['offerFcSet'] = offerFcSet;
    var offerFcSetKey = adUnit['offerfcKey'];
    Vijs.Cache.setLocalStorage(offerFcSetKey, offerFcSet);
    return offerFcSet;
  },
  getOfferFcSet: function (adUnit) {
    var offerFcSetKey = adUnit['offerfcKey'];
    var offerFcSet = Vijs.Cache.getLocalStorage(offerFcSetKey);
    Vijs.Configer.isLogShow && console.log('unit fc set is loaded');
    Vijs.Configer.isLogShow && console.log(offerFcSet);
    return offerFcSet;
  },
  getOfferFcIds: function (adUnit) {
    var exclude_ids = [];
    var offerFcSet = adUnit['offerFcSet'];
    //console.log(offerFcSet);
    var now = (new Date()).getTime();
    for (var key in offerFcSet) {
      var fca = offerFcSet[key]['fca'];
      var fcaTime = offerFcSet[key]['fcaTime'];
      var fcArray = offerFcSet[key]['fcaArray'];
      if (fcArray.length >= fca && Vijs.Util.dateDiff(fcArray[0], now, 'second') < fcaTime) {
        exclude_ids.push(parseInt(key));
      }
    }
    return JSON.stringify(exclude_ids);
  },
  getOfferTtcIds: function (adUnit) {
    var aRes = [];
    var aVbaAds = adUnit['aVbaAds'];
    var expiredTime = adUnit.setting.data.getpf;
    var now = (new Date()).getTime();
    for (var i = 0; i < aVbaAds.length; i++) {
      var time = aVbaAds[i].ts;
      if (Vijs.Util.dateDiff(time, now, 'second') < expiredTime) {
        aRes.push(aVbaAds[i].id);
      } else {
        // del
        aVbaAds.splice(i, 1);
      }
    }
    return JSON.stringify(aRes);
  },
  getVbaOffer: function (adUnit) {
    var vabofferKey = adUnit['vabofferKey'];
    var aVbaAds = Vijs.Cache.getLocalStorage(vabofferKey);
    return aVbaAds ? aVbaAds : [];
  },
  cacheVbaOffer: function (aVba, adUnit) {
    var aVbaAds = this.getVbaOffer(adUnit);
    var aRes = aVbaAds.concat(aVba);
    var vabofferKey = adUnit['vabofferKey'];
    Vijs.Cache.setLocalStorage(vabofferKey, aRes);
  },
  diffVbaOffer: function (aTmp, adUnit) {
    var aVbaOffer = this.getVbaOffer(adUnit);
    if (!aVbaOffer.length) {
      return aTmp;
    }
    var aRes = [];
    for (var i = 0; i < aTmp.length; i++) {
      if (!_findArr(aVbaOffer, aTmp[i].id)) {
        aRes.push(aTmp[i]);
      }
    }
    // ttc_ids is less pcrn
    if (aRes.length > adUnit.pcrn) {
      var n = aRes.length - adUnit.pcrn;
      aRes = aTmp.splice(n, adUnit.pcrn);
    }
    return aRes;

    function _findArr(arr, n) {
      for (var i = 0; i < arr.length; i++) {
        if (arr[i].id == n) return true;
      }
      return false;
    }

  }

};

